import React from "react"
import data from './data.json'

import Card from '../Cards/Card'

export default (props) => (
    <>
        <div id='block-2' className="row justify-content-center align-items-center mr-0">
            <div className='col-12'>
                <h4 class="text-center text-white">Bolsas e pontuações</h4>
                <p class="text-center text-white">Todas as bolsas serão concedidas durente todo o curso.</p>
            </div>
            <div className="col-7">
                <div className="row">
                    {data.map(el=>(
                        <div className="col-xs-12 col-sm-4 col-lg-3 pr-5 pt-3">
                            <Card
                                text={el.text}
                                color={el.color}
                                porcent={el.porcent}
                            />
                        </div>
                    ))}
                </div>
            </div>
        </div>

        <img id='purpleWave-image' className="w-100" src='/images/purpleWave.svg'/>
    </>
)